,CACA,UAAA,CACA,6BAAA,CACA,iBAAA,CACA,UAAA,CACA,mBAAA,CAGF,+BACE,iBAAA,CACA,SAAA,CACA,OAAA,CACA,UAAA,CACA,WAAA,CACA,iBAAA,CACA,6BAAA,CACA,mCAAA,CACA,UAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,cAAA,CACA,kBAAA,CACA,0BAAA,CAEA,qCACE,6BAAA,CACA,oBAAA,CAGF,iCACE,cAAA","sourcesContent":["\n.status-bar-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.7);\n  backdrop-filter: blur(4px);\n  z-index: 9999;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 20px;\n  pointer-events: all;\n}\n\n.status-bar-container {\n  position: relative;\n  width: 100%;\n  max-width: 390px;\n  max-height: 90vh;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.phone-frame {\n  position: relative;\n  width: 100%;\n  max-width: 390px;\n  height: 844px;\n  max-height: 90vh;\n  background: #0f172a;\n  border-radius: 40px;\n  overflow: hidden;\n  border: 8px solid #1e1e1e;\n  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);\n  display: flex;\n  flex-direction: column;\n}\n\n.phone-background {\n  position: absolute;\n  inset: 0;\n  pointer-events: none;\n  overflow: hidden;\n\n  .bg-gradient-1 {\n    position: absolute;\n    top: -10%;\n    left: -20%;\n    width: 140%;\n    height: 70%;\n    background: rgba(99, 102, 241, 0.4);\n    filter: blur(80px);\n    border-radius: 50%;\n    opacity: 0.5;\n  }\n\n  .bg-gradient-2 {\n    position: absolute;\n    bottom: -10%;\n    right: -10%;\n    width: 100%;\n    height: 60%;\n    background: rgba(139, 92, 246, 0.3);\n    filter: blur(60px);\n    border-radius: 50%;\n    opacity: 0.5;\n  }\n}\n\n.status-header {\n  position: relative;\n  height: 40px;\n  padding: 0 24px;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  z-index: 50;\n  font-size: 12px;\n  font-weight: 500;\n  color: rgba(255, 255, 255, 0.9);\n  user-select: none;\n}\n\n.header-left {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n}\n\n.back-button {\n  width: 28px;\n  height: 28px;\n  border-radius: 50%;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all 0.2s;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.2);\n    transform: scale(1.1);\n  }\n\n  i {\n    font-size: 14px;\n  }\n}\n\n.header-time {\n  font-size: 12px;\n}\n\n.header-icons {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 10px;\n\n  i {\n    color: rgba(255, 255, 255, 0.6);\n  }\n\n  .signal-text {\n    font-family: monospace;\n    opacity: 0.6;\n  }\n\n  .battery-indicator {\n    width: 24px;\n    height: 12px;\n    border-radius: 2px;\n    border: 1px solid rgba(255, 255, 255, 0.4);\n    position: relative;\n    margin-left: 4px;\n\n    .battery-fill {\n      position: absolute;\n      top: 2px;\n      bottom: 2px;\n      left: 2px;\n      right: 4px;\n      background: rgba(255, 255, 255, 0.9);\n      border-radius: 1px;\n    }\n  }\n}\n\n.status-content {\n  position: relative;\n  flex: 1;\n  overflow-y: auto;\n  z-index: 10;\n\n  &::-webkit-scrollbar {\n    width: 4px;\n  }\n\n  &::-webkit-scrollbar-track {\n    background: transparent;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    background: rgba(255, 255, 255, 0.2);\n    border-radius: 2px;\n  }\n}\n\n.bottom-nav {\n  position: relative;\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  padding: 12px 0;\n  background: rgba(15, 23, 42, 0.8);\n  backdrop-filter: blur(10px);\n  border-top: 1px solid rgba(255, 255, 255, 0.1);\n  z-index: 50;\n}\n\n.nav-button {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 4px;\n  padding: 8px 16px;\n  background: transparent;\n  border: none;\n  color: rgba(255, 255, 255, 0.5);\n  cursor: pointer;\n  transition: all 0.2s;\n  border-radius: 12px;\n\n  i {\n    font-size: 20px;\n    margin-bottom: 2px;\n  }\n\n  span {\n    font-size: 11px;\n    font-weight: 500;\n  }\n\n  &:hover {\n    color: rgba(255, 255, 255, 0.8);\n    background: rgba(255, 255, 255, 0.05);\n  }\n\n  &.active {\n    color: white;\n\n    i {\n      color: #818cf8;\n    }\n  }\n}\n\n.home-indicator {\n  position: absolute;\n  bottom: 4px;\n  left: 50%;\n  transform: translateX(-50%);\n  width: 128px;\n  height: 4px;\n  background: rgba(255, 255, 255, 0.2);\n  border-radius: 2px;\n  z-index: 50;\n  pointer-events: none;\n}\n\n.close-button {\n  position: absolute;\n  top: -50px;\n  right: 0;\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  color: white;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all 0.2s;\n  backdrop-filter: blur(10px);\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.2);\n    transform: scale(1.1);\n  }\n\n  i {\n    font-size: 18px;\n  }\n}\n"],"sourceRoot":""}]);
