ter: blur(10px);\n  border: 1px solid rgba(255, 255, 255, 0.1);\n}\n\n.location-card {\n  background: linear-gradient(135deg, rgba(99, 102, 241, 0.3), rgba(99, 102, 241, 0.1));\n\n  .card-icon {\n    color: #818cf8;\n  }\n}\n\n.coins-card {\n  background: linear-gradient(135deg, rgba(251, 191, 36, 0.3), rgba(251, 191, 36, 0.1));\n\n  .card-icon {\n    color: #fbbf24;\n  }\n\n  .card-value.gold {\n    color: #fcd34d;\n  }\n}\n\n.card-icon {\n  width: 36px;\n  height: 36px;\n  border-radius: 10px;\n  background: rgba(255, 255, 255, 0.1);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 16px;\n}\n\n.card-content {\n  flex: 1;\n  min-width: 0;\n}\n\n.card-label {\n  font-size: 10px;\n  color: rgba(255, 255, 255, 0.5);\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n\n.card-value {\n  font-size: 16px;\n  font-weight: 700;\n  color: white;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.card-sub {\n  font-size: 11px;\n  color: rgba(255, 255, 255, 0.6);\n  font-family: 'JetBrains Mono', monospace;\n}\n\n.character-card {\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.2), rgba(118, 75, 162, 0.1));\n  border-radius: 16px;\n  padding: 16px;\n  border: 1px solid rgba(255, 255, 255, 0.1);\n}\n\n.character-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 16px;\n}\n\n.character-info {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n.character-level {\n  font-size: 20px;\n  font-weight: 700;\n  color: white;\n}\n\n.character-rank {\n  padding: 4px 10px;\n  background: rgba(251, 191, 36, 0.2);\n  border-radius: 12px;\n  font-size: 11px;\n  font-weight: 600;\n  color: #fcd34d;\n}\n\n.combat-stats {\n  display: flex;\n  gap: 12px;\n}\n\n.stat-item {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 6px 12px;\n  background: rgba(255, 255, 255, 0.08);\n  border-radius: 20px;\n\n  i {\n    font-size: 12px;\n\n    &.fa-fire {\n      color: #f87171;\n    }\n\n    &.fa-shield-halved {\n      color: #34d399;\n    }\n  }\n\n  span {\n    font-size: 14px;\n    font-weight: 600;\n    color: white;\n    font-family: 'JetBrains Mono', monospace;\n  }\n}\n\n.status-bars {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n.bar-item {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.bar-header {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 12px;\n\n  i {\n    font-size: 12px;\n    width: 16px;\n    text-align: center;\n  }\n\n  .stamina-icon {\n    color: #34d399;\n  }\n  .lust-icon {\n    color: #f472b6;\n  }\n  .willpower-icon {\n    color: #60a5fa;\n  }\n\n  .bar-label {\n    color: rgba(255, 255, 255, 0.7);\n  }\n\n  .bar-value {\n    margin-left: auto;\n    color: rgba(255, 255, 255, 0.5);\n    font-family: 'JetBrains Mono', monospace;\n    font-size: 11px;\n  }\n}\n\n.bar-track {\n  height: 6px;\n  background: rgba(255, 255, 255, 0.1);\n  border-radius: 3px;\n  overflow: hidden;\n}\n\n.bar-fill {\n  height: 100%;\n  border-radius: 3px;\n  transition: width 0.4s ease;\n\n  &.stamina {\n    background: linear-gradient(90deg, #34d399, #10b981);\n  }\n\n  &.lust {\n    background: linear-gradient(90deg, #f472b6, #ec4899);\n  }\n\n  &.willpower {\n    background: linear-gradient(90deg, #60a5fa, #3b82f6);\n  }\n}\n\n.quick-stats {\n  display: grid;\n  grid-template-columns: repeat(4, 1fr);\n  gap: 8px;\n}\n\n.quick-stat {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding: 10px 8px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  border: 1px solid rgba(255, 255, 255, 0.05);\n\n  .qs-label {\n    font-size: 10px;\n    color: rgba(255, 255, 255, 0.4);\n    margin-bottom: 4px;\n  }\n\n  .qs-value {\n    font-size: 16px;\n    font-weight: 700;\n    font-family: 'JetBrains Mono', monospace;\n\n    &.charm {\n      color: #f472b6;\n    }\n    &.luck {\n      color: #fbbf24;\n    }\n    &.dodge {\n      color: #60a5fa;\n    }\n    &.crit {\n      color: #f87171;\n    }\n  }\n}\n\n.quest-card {\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 14px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  overflow: hidden;\n}\n\n.quest-header {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 12px 14px;\n  background: rgba(255, 255, 255, 0.03);\n  border-bottom: 1px solid rgba(255, 255, 255, 0.05);\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.7);\n\n  i {\n    color: #667eea;\n  }\n}\n\n.quest-content {\n  padding: 14px;\n}\n\n.quest-title {\n  font-size: 14px;\n  font-weight: 600;\n  color: white;\n  margin-bottom: 6px;\n}\n\n.quest-desc {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.6);\n  line-height: 1.5;\n  margin-bottom: 10px;\n}\n\n.quest-status {\n  display: inline-block;\n  padding: 4px 10px;\n  border-radius: 12px;\n  font-size: 11px;\n  font-weight: 500;\n\n  &.active {\n    background: rgba(96, 165, 250, 0.2);\n    color: #93c5fd;\n  }\n\n  &.done {\n    background: rgba(52, 211, 153, 0.2);\n    color: #6ee7b7;\n  }\n}\n\n.time-card {\n  display: flex;\n  align-items: center;\n  gap: 14px;\n  padding: 14px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 14px;\n  border: 1px solid rgba(255, 255, 255, 0.05);\n}\n\n.time-icon {\n  width: 40px;\n  height: 40px;\n  border-radius: 12px;\n  background: linear-gradient(135deg, rgba(96, 165, 250, 0.3), rgba(59, 130, 246, 0.1));\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  i {\n    font-size: 18px;\n    color: #60a5fa;\n  }\n}\n\n.time-info {\n  flex: 1;\n}\n\n.game-time {\n  font-size: 20px;\n  font-weight: 700;\n  color: white;\n  font-family: 'JetBrains Mono', monospace;\n}\n\n.game-date {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.5);\n}\n\n.feed-section {\n  background: rgba(255, 255, 255, 0.02);\n  border-radius: 14px;\n  border: 1px solid rgba(255, 255, 255, 0.05);\n  padding: 14px;\n}\n\n.feed-header {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.7);\n  margin-bottom: 12px;\n\n  i {\n    color: #667eea;\n  }\n}\n\n.feed-list {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.feed-item {\n  display: flex;\n  gap: 10px;\n  align-items: flex-start;\n}\n\n.feed-dot {\n  width: 8px;\n  height: 8px;\n  border-radius: 50%;\n  margin-top: 5px;\n  flex-shrink: 0;\n\n  &.info {\n    background: #60a5fa;\n  }\n  &.warning {\n    background: #fbbf24;\n  }\n  &.success {\n    background: #34d399;\n  }\n  &.error {\n    background: #f87171;\n  }\n}\n\n.feed-content {\n  flex: 1;\n}\n\n.feed-text {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.8);\n  line-height: 1.4;\n}\n\n.feed-time {\n  font-size: 10px;\n  color: rgba(255, 255, 255, 0.4);\n  margin-top: 4px;\n}\n"],"sourceRoot":""}]);
