form 0.2s;\n    color: rgba(255, 255, 255, 0.4);\n\n    &.rotated {\n      transform: rotate(180deg);\n    }\n  }\n}\n\n.quest-card {\n  position: relative;\n  padding: 16px;\n  border-radius: 14px;\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  margin-bottom: 12px;\n  overflow: hidden;\n\n  &.main {\n    background: linear-gradient(135deg, rgba(102, 126, 234, 0.2), rgba(118, 75, 162, 0.1));\n    border-color: rgba(102, 126, 234, 0.3);\n  }\n\n  &.side {\n    background: rgba(255, 255, 255, 0.03);\n\n    &.completed {\n      opacity: 0.6;\n    }\n  }\n}\n\n.quest-badge {\n  position: absolute;\n  top: 12px;\n  right: 12px;\n  padding: 3px 8px;\n  background: linear-gradient(135deg, #667eea, #764ba2);\n  border-radius: 6px;\n  font-size: 9px;\n  font-weight: 700;\n  color: white;\n  letter-spacing: 1px;\n}\n\n.quest-type-badge {\n  position: absolute;\n  top: 12px;\n  right: 12px;\n  padding: 3px 8px;\n  border-radius: 6px;\n  font-size: 10px;\n  font-weight: 600;\n\n  &.type-daily {\n    background: rgba(96, 165, 250, 0.2);\n    color: #93c5fd;\n  }\n\n  &.type-special {\n    background: rgba(167, 139, 250, 0.2);\n    color: #c4b5fd;\n  }\n\n  &.type-timed {\n    background: rgba(251, 191, 36, 0.2);\n    color: #fcd34d;\n  }\n\n  &.type-hidden {\n    background: rgba(248, 113, 113, 0.2);\n    color: #fca5a5;\n  }\n}\n\n.quest-content {\n  padding-right: 60px;\n}\n\n.quest-title {\n  font-size: 15px;\n  font-weight: 600;\n  color: white;\n  margin-bottom: 8px;\n}\n\n.quest-desc {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.6);\n  line-height: 1.5;\n  margin-bottom: 12px;\n}\n\n.quest-meta {\n  display: flex;\n  gap: 12px;\n  margin-bottom: 12px;\n}\n\n.meta-item {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 11px;\n  padding: 4px 10px;\n  border-radius: 12px;\n\n  &.status {\n    &.active {\n      background: rgba(52, 211, 153, 0.15);\n      color: #6ee7b7;\n    }\n\n    &.completed {\n      background: rgba(96, 165, 250, 0.15);\n      color: #93c5fd;\n    }\n\n    &.failed {\n      background: rgba(248, 113, 113, 0.15);\n      color: #fca5a5;\n    }\n\n    &.abandoned {\n      background: rgba(156, 163, 175, 0.15);\n      color: #d1d5db;\n    }\n  }\n\n  &.deadline {\n    background: rgba(251, 191, 36, 0.15);\n    color: #fcd34d;\n  }\n}\n\n.quest-reward {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 10px 12px;\n  background: rgba(251, 191, 36, 0.1);\n  border-radius: 10px;\n  font-size: 12px;\n  color: #fcd34d;\n  margin-bottom: 12px;\n\n  i {\n    color: #fbbf24;\n  }\n\n  &.small {\n    padding: 4px 10px;\n    font-size: 11px;\n    background: transparent;\n    margin-bottom: 0;\n  }\n}\n\n.quest-objectives {\n  padding-top: 12px;\n  border-top: 1px solid rgba(255, 255, 255, 0.08);\n}\n\n.objectives-title {\n  font-size: 11px;\n  color: rgba(255, 255, 255, 0.5);\n  margin-bottom: 10px;\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n\n.objective-list {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.objective-item {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  font-size: 12px;\n\n  i {\n    font-size: 12px;\n\n    &.completed {\n      color: #34d399;\n    }\n\n    &.pending {\n      color: rgba(255, 255, 255, 0.3);\n    }\n  }\n\n  .obj-key {\n    color: rgba(255, 255, 255, 0.8);\n  }\n\n  .obj-value {\n    margin-left: auto;\n    color: rgba(255, 255, 255, 0.5);\n    font-family: 'JetBrains Mono', monospace;\n  }\n}\n\n.quest-footer {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.quest-list {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.completed-list {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.completed-item {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 10px 14px;\n  background: rgba(255, 255, 255, 0.02);\n  border-radius: 10px;\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.5);\n\n  i {\n    color: #34d399;\n    font-size: 14px;\n  }\n}\n\n.empty-state {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 60px 20px;\n  text-align: center;\n\n  .empty-icon {\n    width: 80px;\n    height: 80px;\n    border-radius: 50%;\n    background: rgba(255, 255, 255, 0.03);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    margin-bottom: 20px;\n\n    i {\n      font-size: 32px;\n      color: rgba(255, 255, 255, 0.15);\n    }\n  }\n\n  .empty-title {\n    font-size: 16px;\n    font-weight: 600;\n    color: rgba(255, 255, 255, 0.5);\n    margin-bottom: 6px;\n  }\n\n  .empty-desc {\n    font-size: 13px;\n    color: rgba(255, 255, 255, 0.3);\n  }\n}\n"],"sourceRoot":""}]);
