CACA,wBAAA,CACA,mBAAA,CACA,kBAAA,CAGF,6BACE,YAAA,CACA,mCAAA,CACA,OAAA,CAGF,6BACE,YAAA,CACA,6BAAA,CACA,kBAAA,CACA,gBAAA,CACA,8BAAA,CACA,iBAAA,CACA,cAAA,CAGF,8BACE,wBAAA,CAGF,8BACE,eAAA,CACA,sCAAA,CAEA,uCACE,aAAA,CAGF,uCACE,aAAA,CAGF,sCACE,wBAAA","sourcesContent":["\n.profile-page {\n  padding: 16px 20px;\n  overflow-y: auto;\n  flex: 1;\n}\n\n.profile-header {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding-top: 16px;\n  margin-bottom: 24px;\n}\n\n.profile-avatar {\n  width: 88px;\n  height: 88px;\n  border-radius: 50%;\n  background: linear-gradient(135deg, #667eea, #764ba2);\n  padding: 3px;\n  box-shadow: 0 8px 32px rgba(102, 126, 234, 0.4);\n  cursor: pointer;\n  position: relative;\n  transition: all 0.3s ease;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  &:hover {\n    transform: scale(1.05);\n    box-shadow: 0 12px 40px rgba(102, 126, 234, 0.5);\n\n    .avatar-upload-hint {\n      opacity: 1;\n    }\n  }\n\n  .avatar-inner {\n    width: 100%;\n    height: 100%;\n    border-radius: 50%;\n    background: #1a1f35;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    overflow: hidden;\n  }\n\n  .avatar-image {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: 50%;\n  }\n\n  i {\n    font-size: 36px;\n    color: rgba(255, 255, 255, 0.6);\n  }\n\n  .avatar-upload-hint {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    width: 28px;\n    height: 28px;\n    background: linear-gradient(135deg, #667eea, #764ba2);\n    border-radius: 50%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    opacity: 0;\n    transition: opacity 0.2s;\n    border: 2px solid #1a1f35;\n    z-index: 10;\n\n    i {\n      font-size: 12px;\n      color: white;\n    }\n  }\n}\n\n.profile-name {\n  margin-top: 12px;\n  font-size: 18px;\n  font-weight: 700;\n  color: white;\n  letter-spacing: 0.5px;\n}\n\n.profile-level {\n  display: flex;\n  gap: 8px;\n  margin-top: 8px;\n\n  .level-badge,\n  .potential-badge {\n    padding: 4px 12px;\n    border-radius: 20px;\n    font-size: 12px;\n    font-weight: 600;\n  }\n\n  .level-badge {\n    background: linear-gradient(135deg, #667eea, #764ba2);\n    color: white;\n  }\n\n  .potential-badge {\n    background: rgba(255, 255, 255, 0.1);\n    color: rgba(255, 255, 255, 0.8);\n    border: 1px solid rgba(255, 255, 255, 0.2);\n  }\n}\n\n.stats-grid {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 10px;\n  margin-bottom: 20px;\n}\n\n.stat-card {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  padding: 14px;\n  border-radius: 16px;\n  backdrop-filter: blur(10px);\n  border: 1px solid rgba(255, 255, 255, 0.1);\n  transition: all 0.2s ease;\n\n  &:hover {\n    transform: translateY(-2px);\n    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);\n  }\n\n  &.charm {\n    background: linear-gradient(135deg, rgba(236, 72, 153, 0.2), rgba(236, 72, 153, 0.05));\n    .stat-icon {\n      color: #ec4899;\n    }\n    .stat-value {\n      color: #f472b6;\n    }\n  }\n\n  &.luck {\n    background: linear-gradient(135deg, rgba(251, 191, 36, 0.2), rgba(251, 191, 36, 0.05));\n    .stat-icon {\n      color: #fbbf24;\n    }\n    .stat-value {\n      color: #fcd34d;\n    }\n  }\n\n  &.combat {\n    background: linear-gradient(135deg, rgba(239, 68, 68, 0.2), rgba(239, 68, 68, 0.05));\n    .stat-icon {\n      color: #ef4444;\n    }\n    .stat-value {\n      color: #f87171;\n    }\n  }\n\n  &.endurance {\n    background: linear-gradient(135deg, rgba(52, 211, 153, 0.2), rgba(52, 211, 153, 0.05));\n    .stat-icon {\n      color: #34d399;\n    }\n    .stat-value {\n      color: #6ee7b7;\n    }\n  }\n\n  .stat-icon {\n    font-size: 20px;\n    width: 32px;\n    height: 32px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  }\n\n  .stat-info {\n    flex: 1;\n  }\n\n  .stat-label {\n    font-size: 11px;\n    color: rgba(255, 255, 255, 0.5);\n    text-transform: uppercase;\n    letter-spacing: 0.5px;\n  }\n\n  .stat-value {\n    font-size: 20px;\n    font-weight: 700;\n  }\n}\n\n.detail-card {\n  padding: 16px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 16px;\n  backdrop-filter: blur(10px);\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  margin-bottom: 16px;\n}\n\n.detail-title {\n  font-size: 13px;\n  font-weight: 600;\n  color: rgba(255, 255, 255, 0.7);\n  margin-bottom: 16px;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n\n  i {\n    font-size: 14px;\n    color: #667eea;\n  }\n}\n\n.detail-list {\n  display: flex;\n  flex-direction: column;\n  gap: 14px;\n}\n\n.progress-item {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.progress-header {\n  display: flex;\n  justify-content: space-between;\n  font-size: 12px;\n  color: white;\n\n  span:first-child {\n    display: flex;\n    align-items: center;\n    gap: 6px;\n\n    i {\n      font-size: 12px;\n      opacity: 0.7;\n    }\n  }\n\n  .progress-value {\n    color: rgba(255, 255, 255, 0.5);\n    font-family: 'JetBrains Mono', monospace;\n    font-size: 11px;\n  }\n}\n\n.progress-bar {\n  height: 6px;\n  background: rgba(255, 255, 255, 0.1);\n  border-radius: 3px;\n  overflow: hidden;\n}\n\n.progress-fill {\n  height: 100%;\n  border-radius: 3px;\n  transition: width 0.4s ease;\n\n  &.stamina {\n    background: linear-gradient(90deg, #34d399, #10b981);\n  }\n\n  &.lust {\n    background: linear-gradient(90deg, #f472b6, #ec4899);\n  }\n\n  &.willpower {\n    background: linear-gradient(90deg, #60a5fa, #3b82f6);\n  }\n\n  &.exp {\n    background: linear-gradient(90deg, #fbbf24, #f59e0b);\n  }\n}\n\n.attribute-grid {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 12px;\n}\n\n.attribute-item {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 12px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 10px;\n  border: 1px solid rgba(255, 255