: column;\n  }\n\n  .coins-label {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.6);\n    text-transform: uppercase;\n    letter-spacing: 0.5px;\n  }\n\n  .coins-amount {\n    font-size: 28px;\n    font-weight: 700;\n    color: #fcd34d;\n    font-family: 'JetBrains Mono', monospace;\n  }\n}\n\n.section-card {\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 16px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  padding: 16px;\n  margin-bottom: 16px;\n}\n\n.section-title {\n  font-size: 13px;\n  font-weight: 600;\n  color: rgba(255, 255, 255, 0.7);\n  margin-bottom: 16px;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n\n  i {\n    font-size: 14px;\n    color: #667eea;\n  }\n\n  .item-count {\n    margin-left: auto;\n    font-size: 11px;\n    color: rgba(255, 255, 255, 0.4);\n    font-weight: 400;\n  }\n}\n\n.equipment-grid {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.equipment-slot {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  padding: 12px;\n  background: rgba(255, 255, 255, 0.03);\n  border-radius: 12px;\n  border: 1px solid rgba(255, 255, 255, 0.08);\n  transition: all 0.2s ease;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.05);\n  }\n\n  &.empty {\n    opacity: 0.5;\n  }\n\n  .slot-icon {\n    width: 36px;\n    height: 36px;\n    border-radius: 10px;\n    background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.1));\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    i {\n      font-size: 16px;\n      color: #a78bfa;\n    }\n  }\n\n  .slot-info {\n    flex: 1;\n  }\n\n  .slot-label {\n    font-size: 10px;\n    color: rgba(255, 255, 255, 0.4);\n    text-transform: uppercase;\n    letter-spacing: 0.5px;\n  }\n\n  .slot-value {\n    font-size: 14px;\n    color: white;\n    font-weight: 500;\n    margin-top: 2px;\n  }\n}\n\n.bonus-grid {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  gap: 8px;\n}\n\n.bonus-item {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 12px;\n  background: rgba(255, 255, 255, 0.02);\n  border-radius: 10px;\n  border: 1px solid rgba(255, 255, 255, 0.05);\n}\n\n.bonus-label {\n  font-size: 12px;\n  color: rgba(255, 255, 255, 0.6);\n}\n\n.bonus-value {\n  font-size: 14px;\n  font-weight: 600;\n  font-family: 'JetBrains Mono', monospace;\n\n  &.positive {\n    color: #34d399;\n  }\n\n  &.negative {\n    color: #f87171;\n  }\n\n  &.neutral {\n    color: rgba(255, 255, 255, 0.4);\n  }\n}\n\n.inventory-list {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.inventory-item {\n  padding: 14px;\n  background: rgba(255, 255, 255, 0.02);\n  border-radius: 12px;\n  border-left: 3px solid;\n  transition: all 0.2s ease;\n\n  &:hover {\n    background: rgba(255, 255, 255, 0.05);\n    transform: translateX(4px);\n  }\n\n  &.rarity-c {\n    border-left-color: #9ca3af;\n  }\n\n  &.rarity-b {\n    border-left-color: #60a5fa;\n  }\n\n  &.rarity-a {\n    border-left-color: #a78bfa;\n  }\n\n  &.rarity-s {\n    border-left-color: #fbbf24;\n  }\n\n  &.rarity-ss {\n    border-left-color: #f87171;\n  }\n\n  .item-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 6px;\n  }\n\n  .item-name {\n    font-size: 14px;\n    font-weight: 600;\n    color: white;\n  }\n\n  .item-badges {\n    display: flex;\n    gap: 6px;\n    align-items: center;\n  }\n\n  .item-level {\n    padding: 2px 8px;\n    border-radius: 10px;\n    font-size: 10px;\n    font-weight: 600;\n\n    &.rarity-c {\n      background: rgba(156, 163, 175, 0.2);\n      color: #d1d5db;\n    }\n\n    &.rarity-b {\n      background: rgba(96, 165, 250, 0.2);\n      color: #93c5fd;\n    }\n\n    &.rarity-a {\n      background: rgba(167, 139, 250, 0.2);\n      color: #c4b5fd;\n    }\n\n    &.rarity-s {\n      background: rgba(251, 191, 36, 0.2);\n      color: #fcd34d;\n    }\n\n    &.rarity-ss {\n      background: rgba(248, 113, 113, 0.2);\n      color: #fca5a5;\n    }\n  }\n\n  .item-count {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.5);\n    font-family: 'JetBrains Mono', monospace;\n  }\n\n  .item-type {\n    font-size: 11px;\n    color: rgba(255, 255, 255, 0.4);\n    margin-bottom: 6px;\n  }\n\n  .item-desc {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.6);\n    line-height: 1.5;\n  }\n}\n\n.empty-state {\n  padding: 40px 20px;\n  text-align: center;\n\n  i {\n    font-size: 48px;\n    color: rgba(255, 255, 255, 0.1);\n    margin-bottom: 16px;\n  }\n\n  p {\n    font-size: 14px;\n    color: rgba(255, 255, 255, 0.5);\n    margin-bottom: 4px;\n  }\n\n  span {\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.3);\n  }\n}\n"],"sourceRoot":""}]);
